import * as React from "react";

export default function HeroHighlight() {
  return (
    <div className={`row p-4`} style={{ fontFamily: "Playfair Display" }}>
      <div className="col text-center">
        <h1 className="mb-4" style={{ fontSize: "4em", fontWeight: 900 }}>
          We Create Awesome Stuff for your Brands
        </h1>
      </div>
    </div>
  );
}
