import * as React from "react";
import { Link } from "gatsby";
import logoImg from "../images/Lane7(TriColor).svg";

const pageStyles = {
  fontFamily: "Playfair Display",
  fontWeight: 900,
};

const NavHeader = ({ pageTitle, children }) => {
  return (
    <div className="container" style={pageStyles}>
      <div className="d-flex flex-wrap align-items-center justify-content-center">
        <Link
          to="/"
          className="d-flex align-items-center my-2 me-auto text-decoration-none"
        >
          <h1 className="mx-2 text-dark" style={pageStyles}>
            Diggital
          </h1>
        </Link>

        <Link
          to="/"
          className="d-flex align-items-center my-2 me-auto text-decoration-none"
        >
          <img
            alt="Lane7"
            src={logoImg}
            width={150}
            height={120}
            className="d-inline-block align-text-top"
          />
        </Link>

        <ul className="nav col-12 col-lg-auto my-2 justify-content-center my-md-0">
          <li>
            <Link to="/" className="nav-link text-dark font-weight-bold">
              Home
            </Link>
          </li>
          <li>
            <Link to="/" className="nav-link">
              Portfolio
            </Link>
          </li>
          <li>
            <Link to="/" className="nav-link">
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default NavHeader;
